.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: var(--m10);
	height: 100%;
}
.headerWrapper{
	width: 100%;
	background-color: #1C1C21;
}
.main{
	margin-top: var(--m10);
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: var(--m15)
}